import React from 'react';
import { sendNotification } from '../../redux/actions';
import Box from '@material-ui/core/Box';
import GridContainer from '../../@crema/core/GridContainer';
import Grid from '@material-ui/core/Grid';
// import { AppState } from '../../redux/store';
import { Button, TextField } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useDispatch } from 'react-redux';

import Chip from '@mui/material/Chip';
import Autocomplete from '@mui/material/Autocomplete';

const useStyles = makeStyles((theme) => ({
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: '100%',
        padding: '0'
    },
}));
const Account = () => {
    const classes = useStyles();
    const dispatch = useDispatch()

    const onSendNotify = () => {
        const data = {
            title,
            iconUrl,
            image,
            text,
            userNo,
            userRange: userRange.replaceAll('#', ''),
            sysValue,
            methodValue
        }
        dispatch(sendNotification(data))
    }

    const systems = [
        { title: 'Android', value: 'android' },
        { title: 'Windows', value: 'windows' },
        { title: 'Mac', value: 'mac' },
        { title: 'Others', value: 'others' }
    ];
    const custom = ['Individual', 'Partial', 'Total'];

    const [title, setTitle] = React.useState('');
    const [iconUrl, setIconURL] = React.useState('');
    const [image, setImage] = React.useState('');
    const [text, setText] = React.useState('');
    const [userRange, setUserRange] = React.useState('');
    const [userNo, setUserNo] = React.useState('');
    const [sysValue, setSysValue] = React.useState([systems[0]]);
    const [methodValue, setMethodValue] = React.useState<string | null>('Individual');
    return (
        <>
            <Box pt={{ xl: 4 }}>
                <Grid item component='h1' style={{ display: 'flex', justifyContent: 'center', paddingBottom: '10px' }}>Send Notification</Grid>
                <GridContainer style={{ padding: '0 100px' }}>
                    <Grid item xs={12} sm={6} md={6} style={{ padding: '0 10px' }}>
                        <TextField
                            id='outlined-confirm-password'
                            label='Enter Title'
                            defaultValue=''
                            name='confirm-password'
                            className={classes.textField}
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            margin='normal'
                            variant='outlined'
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} style={{ padding: '0 10px' }}>
                        <TextField
                            id='outlined-confirm-password'
                            label='Enter Icon URL'
                            defaultValue=''
                            name='confirm-password'
                            className={classes.textField}
                            value={iconUrl}
                            onChange={(e) => setIconURL(e.target.value)}
                            margin='normal'
                            variant='outlined'
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} style={{ padding: '0 10px' }}>
                        <TextField
                            id='outlined-confirm-password'
                            label='Input Image URL'
                            defaultValue=''
                            name='confirm-password'
                            className={classes.textField}
                            value={image}
                            onChange={(e) => setImage(e.target.value)}
                            margin='normal'
                            variant='outlined'
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} style={{ padding: '0 10px' }}>
                        <TextField
                            id='outlined-confirm-password'
                            label='Enter Content'
                            defaultValue=''
                            name='confirm-password'
                            className={classes.textField}
                            value={text}
                            multiline
                            rows={3}
                            onChange={(e) => setText(e.target.value)}
                            margin='normal'
                            variant='outlined'
                            style={{ height: '100px' }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} style={{ padding: '10px 15px' }}>
                        <Autocomplete
                            multiple
                            id="systems"
                            value={sysValue}
                            onChange={(event, newValue) => {
                                setSysValue([...newValue]);
                            }}
                            options={systems}
                            getOptionLabel={(option) => option.title}
                            renderTags={(tagValue, getTagProps) =>
                                tagValue.map((option, index) => (
                                    <Chip
                                        label={option.title}
                                        {...getTagProps({ index })}
                                    />
                                ))
                            }
                            renderInput={(params) => (
                                <TextField {...params} label="Fixed tag" placeholder="Favorites" />
                            )}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} style={{ padding: '10px 15px' }}>

                        <Autocomplete
                            id="controllable-states-demo"
                            value={methodValue}
                            onChange={(event: any, newValue: string | null) => {

                                setMethodValue(newValue);
                            }}
                            options={custom}
                            renderInput={(params) => <TextField {...params} label="Controllable" />}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={8}>
                        <TextField
                            id='outlined-confirm-password'
                            label={methodValue === 'Individual' ? 'Enter User No: #000, 000' : 'Enter User Range: #00-#000, 00-000'}
                            type='string'
                            defaultValue=''
                            className={classes.textField}
                            value={methodValue === 'Individual' ? userNo : userRange}
                            onChange={(e) => methodValue === 'Individual' ? setUserNo(e.target.value) : setUserRange(e.target.value)}
                            margin='normal'
                            variant='outlined'
                            disabled={methodValue === 'Total' ? true : false}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} >
                        <Button
                            variant='contained'
                            color='primary'
                            type='button'
                            size='large'
                            onClick={() => onSendNotify()}
                            style={{ margin: '20px 0 0 100px' }}
                            disabled={title.length === 0 ? true : false}
                        >
                            Send Notify
                        </Button>
                    </Grid>
                </GridContainer>
            </Box>
        </>
    );
};

export default Account;
